* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.link {
  color: white;
  text-decoration: none;
} 

li {
  list-style: none;
  cursor: pointer;
}

input {
  color: black;
  height: 30px;
  border: 2px solid white;
}

.Nav {
  background: black;
  color: white;
}

.Home {
  background: black;
  color: white;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
}

.header h2 {
  cursor: pointer;
}

.header span {
  font-size: 2rem;
  cursor: pointer;
}

.main img{
  width: 100%;
}

.card {
  margin: 20px;
}

.page1 {
  display: flex;
  margin: 10px;
}

.page1 img {
  width: 100%;
}

.page1detail {
  margin: 5px 0;
}

.menu {
  width: 30px;
  height: 100%;
  margin: 5px 20px 20px;
  float: right;
  position: relative;
  z-index: 9;
}

.menu li {
  display: flex;
  justify-content: right;
  font-size: 1.4rem;
  line-height: 50px;
}

footer {
  padding: 20px 20px 40px;
  background: #333;
}

footer span {
  text-decoration: underline;
  cursor: pointer;
}