* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
  }

  button {
    color: black;
    background: white;
    width: 100px;
    padding: 15px;
    font-size: 1.2rem;
    cursor: pointer;
  }

  hr {
    margin: 10px 0;
  }

  .Detail {
    background: black;
    color: white;
  }

  .logo {
    position: relative;
  }

  .bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background: url('../img/detail_logo2.png');
  }

  .alert {
    color: red;
  }

  .product {
    margin: 20px;
  }

  .product span {
    cursor: pointer;
  }

  .snap_btn {
    display: flex;
    justify-content: space-between;
  }

  .snap_btn button {
    width: 45%;
    margin-bottom: 20px;
  }

  .review img {
    width: 100%;
  }

  .review_detail {
    padding: 20px;
  }

  .review_detail h2, p {
    margin: 10px 0;
  }

  .review_detail button {
    margin-left: 1px;
  }

  .chat {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .chat> input {
    width: 70%;
    margin-right: 30px;
  }

  .chat> button {
    background: blue;
    color: white;
    height: 30px;
    font-size: 1rem;
    padding: 5px;
  }

  .styled img {
    width: 100%;
  }

